import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import {BigNumberCaptionCard,ReviewCard,ProgressBarCard} from '../Cards';
import { CircularProgress,Box, makeStyles,Typography,Grid,Paper, Tab, Tabs,useTheme, Container,Button, ListItem, ListItemAvatar, Avatar, IconButton, ListItemSecondaryAction, ListItemText, List  } from '@material-ui/core';
import { Rating} from '@material-ui/lab';
import StarIcon from '@material-ui/icons/Star';
import { Link } from 'react-router-dom';
import ReactPlayer from 'react-player/youtube';
import PlayCircleFilledRoundedIcon from '@material-ui/icons/PlayCircleFilledRounded';






const useStyles = makeStyles(({spacing,palette,breakpoints})=>({
    root:{
        color:'rgba(226, 226, 226,1)'
    },
    fold:{
        [breakpoints.up('md')]:{
            margin:spacing('auto',20)
        },
        [breakpoints.up('lg')]:{
            margin:spacing('auto',40)
        }
    },
    preview:{
        // backgroundImage:"linear-gradient(0deg, rgba(0, 0, 0, 0.4) 40%, rgba(0, 0, 0, 0)),url(/img/gallary/books-768426_1920.jpg)",
        // backgroundPosition:'center',
        // backgroundSize:'cover',
        // backgroundRepeat:'no-repeat',
        // height:spacing(25),
        backgroundColor:'black',
        padding:spacing(0,6,0,6),
        [breakpoints.down('sm')]:{
            padding:spacing(2,2)
        }
    },
    bgTransparent:{
        backgroundColor:'transparent'
    },
    bgGrey:{
        backgroundColor:palette.background.default
    },
    bgSurface:{
        backgroundColor:palette.background.paper
    },
    contained:{
        borderRadius:spacing(3)
    },
    listCardRoundTop:{
        borderRadius:spacing(1,1,0,0),
        width:'100%'
    },
    listCardRoundBottom:{
        borderRadius:spacing(0,0,1,1),
        width:'100%'
    },
    listSpacing:{
        margin:spacing(0.5,0),
    },
    h6:{
        color:palette.primary.contrastText
    },
    caption:{
        color:palette.primary.contrastText
    },
    listHover:{
        '&:hover':{
            backgroundColor:palette.background.default
        },
        '&:active':{
            backgroundColor:palette.primary.light
            
        }
    }
}))

function TabPanel(props) {
    const { children, value, index, ...other } = props;


  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`full-width-tabpanel-${index}`}
        aria-labelledby={`full-width-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };

  function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`,
    };
  }


function CoursePlayer() {

    const classes = useStyles();
    const [showModal, setShowModal] = React.useState(false);
    const [value, setValue] = React.useState(1);
    const theme = useTheme();
    const [state,setState] = React.useState({
        isLoaded:false,
        error:false,
        course:{}
    })
    const [lesson,setLesson] = React.useState(null);
    const handleChange = (event, newValue) => {
        setValue(newValue);
      };
    
      const handleChangeIndex = (index) => {
        setValue(index);
      };
      

    const fooTop=(index,array)=>{
        return index===0 && array.length>1
    }



    const fooBottom = (index,array)=>{
            return index===array.length-1 && array.length>1
    }


    const fooSquare = (index,array)=>{
            return (index<array.length-1 && index!==0) || array.length===1
    }
    
    React.useEffect(() => {
        if(!state.isLoaded){
            fetch("/store/db.json")
            .then(res => res.json())
            .then(
              (result) => {
                console.log(result.courses[0])    
                  
                setState((prev)=>{
                    return {isLoaded:true,
                        error:false,
                        course:result.courses[0]}
                });
                
                
                
               
              },
              // Note: it's important to handle errors here
              // instead of a catch() block so that we don't swallow
              // exceptions from actual bugs in components.
              (error) => {
                setState({
                  isLoaded: true,
                  error
                });
              });
        }
        


        return () => {
          
        };
      });


    return (
        <>
        <Box component="div" className={classes.preview}>
            <Grid
              container
              spacing={1}
              direction="row"
              justify="center"
              alignItems="center"
              alignContent="center"
              wrap="nowrap"
              
            >
                
                {state.isLoaded?
                (<ReactPlayer
                playing
                controls
                width='100%'
                height='100%'
                url={
                    lesson?lesson.videoUrl:
                    state.isLoaded?state.course.lessons[0].videoUrl:''}
                />):(
                    <Box p={10}>
                        <CircularProgress />
                        
                    </Box>
                )

                }
              
            </Grid>
            <Typography variant="h6" color="initial" gutterBottom className={classes.h6}>
                Learn how to play the electric guitar
            </Typography>
            <Grid
                container
                direction="column"
                justify="space-between"
                alignItems="stretch"
                
            >
                {/* <Grid item xs={12}>
                    <Typography variant="h6" className={classes.h6}>
                    Street photography fro beginners
                    </Typography>
                </Grid> */}
                <Grid item xs={12}>
                    <Grid
                    spacing={7}
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                    
                    >
                    <Grid item xs={6}>
                        <Typography component={Link} to="/profile" variant="caption" className={classes.caption}>
                            By Francisco Aguilera G
                        </Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <Rating
                        readOnly
                        size="small"
                        defaultValue={2} 
                        emptyIcon={<StarIcon fontSize="inherit" className={classes.root} />}
                         />
                    </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Box>

        {/* progress bar box area */}
        {
            Boolean(showModal)||(

                <Box mt={-2} className={classes.bgTransparent}>
                    <Grid container
                    direction="column"
                    justify="center"
                    alignItems="center"
                    >
                        <ProgressBarCard/>
                        <Box mt={-3}
                        >
                            <Button
                            variant="contained"
                            className={classes.contained}
                            color="primary"
                            onClick={()=>
                                setShowModal((prev)=>!prev)
                                
                            }
                            >
                                Continue Learning
                            </Button>
                        </Box>
                    
                    </Grid>
                </Box>
            )
        }
        {/* progress bar box area */}
        
        <Box component="div" className={classes.bgGrey}>
        <Box component="div" pt={2} px={4}>
        <Paper elevation={1}>
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
            <Tab label="INFO"  {...a11yProps(0)} />
            <Tab label="lessons"  {...a11yProps(1)}/>
            <Tab label="review"   {...a11yProps(2)}/>
        </Tabs>
        </Paper>
        </Box>

        {/* tab body */}
        <Box component="div">
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={value}
            onChangeIndex={handleChangeIndex}
        >
            {/* info tab panel */}
            <TabPanel value={value} index={0} dir={theme.direction}>
                <Container maxWidth="md" >
                    <Box component="div">
                        <Grid
                        container
                        direction="row"
                        justify="space-between"
                        alignItems="flex-end"
                         >
                            
                               
                                    <Grid item xs={4}>
                                        <BigNumberCaptionCard
                                        cardTitle={state.isLoaded?state.course.lessons.length:''}
                                        cardCaption="lessons" 
                                        />
                                    </Grid>

                                    <Grid item xs={4}>
                                        <BigNumberCaptionCard
                                        cardTitle={state.isLoaded?state.course.studentCount:''}
                                        cardCaption="student" 
                                        />
                                    </Grid>
                               
                            
                          
                        </Grid>
                    </Box>
                    <Box component="div" pt={3}>
                        <Typography variant="subtitle2" color="initial" align='justify'>
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Temporibus magni iure non laudantium perspiciatis enim sunt, veniam repellat ex ducimus natus, pariatur totam. Eum, voluptatum excepturi voluptatibus culpa illo reprehenderit reiciendis aut, quam, aperiam illum totam dolor non accusamus quas?
                        </Typography>
                    </Box>
                    
                  
                </Container>
            </TabPanel>

            {/* lesson tab panel */}
            <TabPanel value={value} index={1} dir={theme.direction}>
                
                    {/* list  */}
                    
                        <List dense className={classes.fold}>
                        {
                            state.isLoaded?
                                state.course.lessons.map((value,index,array)=>(
                                    <Paper 
                                    key={value.id}
                                    onClick={()=>setLesson(value)}
                                    elevation={4} 
                                    square={fooSquare(index,array)} 
                                    className={`${fooTop(index,array)?classes.listCardRoundTop:''} 
                                    ${fooBottom(index,array)?classes.listCardRoundBottom:''}
                                    ${classes.listSpacing}
                                    `}
                                     >
                                    <ListItem className={classes.listHover} >
                                            <ListItemAvatar>
                                                <Avatar>
                                                {index+1}
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary={value.title}
                                                secondary={value.type}
                                            />
                                            <ListItemSecondaryAction>
                                                <IconButton onClick={()=>setLesson(value)} edge="end" aria-label="delete">
                                                <PlayCircleFilledRoundedIcon color="primary" />
                                                </IconButton>
                                            </ListItemSecondaryAction>
                                    </ListItem>
                                </Paper>
                                )):null
                        }
                        
                        
                        </List>
                    
                    {/* list with secondary text */}
                
            </TabPanel>

            {/* review tab panel */}
            <TabPanel value={value} index={2} dir={theme.direction}>
            {/* review */}
            <Box className={classes.fold}>
                {
                    [1,2,3].map((value,index)=>(
                        <Box mb={3}>
                            <ReviewCard
                            key={index}
                            title={"Aniekan"}
                            rating={value}
                            review={"Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptate qui neque, minima doloremque eaque temporibus laudantium, sequi alias eius reprehenderit eum asperiores voluptas amet obcaecati dolorum, harum enim fugiat vel!"}
                            />
                        </Box>

                    ))
                }
            </Box>
            {/* review */}
            </TabPanel>
        </SwipeableViews>
        </Box>
            <Box component="div" py={3} px={4} className={`${classes.bgSurface} ${classes.fold}`}>
                {/* {
                    start||
                <Button onClick={setStart(true)} variant="contained" color="primary" fullWidth
                    className={classes.contained}
                >
                    Start taking Course
                </Button>
                } */}
            </Box>
        </Box>
        </>
    )
}

export default CoursePlayer;
