import React,{useState} from 'react';
import { List, Box, Typography, Button, Grid, makeStyles } from '@material-ui/core';
import {ListViewCard} from '../Cards';

const useStyles = makeStyles(({palette,spacing,breakpoints})=>({
    fold:{
        [breakpoints.up('md')]:{
            margin:spacing(4,20)
        },
        [breakpoints.up('sm')]:{
            margin:spacing(4,10)
        }
    },
    text:{
        fontWeight:600,
        textTransform:'capitalize'
    }
}))


export default function FeedPage(props) {
    const classes = useStyles();
    const [state,setState] = useState({
        isLoaded:false,
        error:false,
        courses:[]
    })


    React.useEffect(() => {
        if(!state.isLoaded){
            fetch("/store/db.json")
            .then(res => res.json())
            .then(
              (result) => {
                console.log(result.courses)    
                  
                setState((prev)=>{
                    return {isLoaded:true,
                        error:false,
                        courses:result.courses}
                });
                
                
                
               
              },
              // Note: it's important to handle errors here
              // instead of a catch() block so that we don't swallow
              // exceptions from actual bugs in components.
              (error) => {
                setState({
                  isLoaded: true,
                  error
                });
              });
        }
        


        return () => {
          
        };
      });
    return (
        <>
            {/* featured courses section */}
            <Box className={classes.fold}>
                <Box px={2} mt={2}>
                    <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                    >
                        <Typography variant="h5" color="initial">
                            Featured courses
                        </Typography>
                        <Button variant="text" className={classes.text} color="primary" size="large">
                            See all
                        </Button>
                    </Grid>
                </Box>
                <List>
                    <Grid
                    container
                    spacing={1}
                    direction="row"
                    justify="flex-start"
                    alignItems="center"
                    >
                    {
                        state.isLoaded?(
                            state.courses.map((value,index)=>(
                                    <Grid key={index} item xs={12} sm={6} md={4}>
                                        <ListViewCard primaryText={value.title} secondaryText="web" imageUrl={value.imageUrl} key={index} rating={value.rating} />
                                    </Grid>
                                  
                                  ))

                        ):(null)
                            }
                            </Grid>
                </List>
            </Box>

            {/* new courses section */}
            <Box className={classes.fold}>
                <Box px={2} mt={2}>
                    <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                    >
                        <Typography variant="h5" color="initial">
                            New Courses
                        </Typography>
                        <Button variant="text" className={classes.text} color="primary" size="large">
                            See all
                        </Button>
                    </Grid>
                </Box>
                <List>
                    <Grid
                    container
                    spacing={1}
                    direction="row"
                    justify="flex-start"
                    alignItems="center"
                    >
                    {
                        state.isLoaded?(
                            state.courses.map((value,index)=>(
                                    <Grid key={index} item xs={12} sm={6} md={4}>
                                        <ListViewCard primaryText={value.title} secondaryText="web" imageUrl="https://picsum.photos/200/300" key={index} rating={value.rating} />
                                    </Grid>
                                  
                                  ))

                        ):(null)
                            }
                            </Grid>
                </List>
            </Box>
        </>
    )
}





